/* Editor */

.GuestBookEditor {
  border: 1px solid gray;
  text-align: center;
  padding: 20px;
}
.GuestBookEditor input {
  margin-bottom: 20px;
  padding: 10px;
}

.GuestBookEditor textarea {
  margin-bottom: 20px;
  width: 500px;
  padding: 10px;
}

.GuestBookEditor button {
  width: 500px;
  padding: 10px;
  cursor: pointer;
}

/* List */
.GuestBookList {
  border: 1px solid gray;
  padding: 20px;
  margin-top: 20px;
}

.GuestBookList h2 {
  text-align: center;
}

.GuestItem {
  background-color: rgb(240, 240, 240);
  margin-bottom: 10px;
  padding: 20px;
}

.GuestItem .info {
  border-bottom: 1px solid gray;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.GuestItem .date {
  color: gray;
}

.GuestItem .content {
  font-weight: bold;
  margin-bottom: 30px;
  margin-top: 30px;
}
